.Header{position:relative;width:100%;background-color:#1e1f26;font-size:18px;padding-bottom:1px}.Header .nav{display:grid;grid-template-columns:auto 1fr auto;column-gap:20px;justify-content:space-between;align-items:center;padding:0 40px;text-align:center}.Header .nav ul{list-style:none;display:grid;grid-auto-flow:column;justify-content:right;margin:0}.Header .nav ul li{position:relative}.Header .nav a,.Header .nav .dropdown{user-select:none;cursor:pointer;display:grid;align-items:center;padding:26px 32px;text-decoration:none;position:relative;box-sizing:border-box;color:#f5f5f5}.Header .nav a:after,.Header .nav .dropdown:after{position:absolute;content:"";height:3px;left:0;bottom:0;background-color:#008b8b;transition:linear all .2s;width:100%;transform:scale(0)}.Header .nav a:hover:after,.Header .nav .dropdown:hover:after{transform:scale(1) !important}.Header .nav a:hover:before,.Header .nav .dropdown:hover:before{transform:rotate(0)}.Header .nav .dropdown:before{transform:rotate(90deg);transition:linear all .1s;content:"â·";position:absolute;right:8px;top:calc(50% - 11px)}.Header .nav .dropdown:hover>ul.dropdown-menu{animation:forwards drop_down .6s .1s}.Header .nav .active:after{position:absolute;content:"";width:100%;height:3px;left:0;bottom:0;background-color:#008b8b;transform:scale(1)}.Header .nav .nav-brand{padding-bottom:2px;font-family:"Comic Sans MS",sans-serif}.Header .nav .nav-brand a:after{transform:scale(0)}.Header .nav:hover .active:after{transform:scale(0)}.Header ul.dropdown-menu{overflow:hidden;max-height:0;position:absolute;display:grid;grid-auto-rows:1fr;grid-auto-columns:1fr;grid-auto-flow:unset;padding:0 0 0 0;top:100%;left:0;width:100%;background-color:#1e1f26;transition:linear all .3s}.Header ul.dropdown-menu li{margin:0;padding:0;width:100%}.Header ul.dropdown-menu li a{padding:12px 6px}@keyframes drop_down{0%{max-height:0}30%{max-height:0}100%{max-height:200px}}